"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { supabase } from "@/lib/supabase";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Megaphone, Calendar, Building2, ExternalLink, ChevronRight, Search } from "lucide-react";

type Announcement = {
    id: string;
    title: string;
    category: string;
    organization: string;
    deadline: string;
    description: string | null;
    source_url: string | null;
    created_at: string;
};

const CATEGORIES = ["전체", "지원사업", "공모전", "교육", "기타"];

const CATEGORY_COLORS: Record<string, { bg: string; text: string; border: string }> = {
    "지원사업": { bg: "bg-blue-50", text: "text-blue-700", border: "border-blue-200" },
    "공모전": { bg: "bg-purple-50", text: "text-purple-700", border: "border-purple-200" },
    "교육": { bg: "bg-green-50", text: "text-green-700", border: "border-green-200" },
    "기타": { bg: "bg-slate-50", text: "text-slate-700", border: "border-slate-200" },
};

function getDDay(deadline: string): { label: string; urgent: boolean } {
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    const deadlineDate = new Date(deadline);
    deadlineDate.setHours(0, 0, 0, 0);
    const diff = Math.ceil((deadlineDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24));
    if (diff < 0) return { label: "마감", urgent: false };
    if (diff === 0) return { label: "D-Day", urgent: true };
    if (diff <= 7) return { label: `D-${diff}`, urgent: true };
    return { label: `D-${diff}`, urgent: false };
}

export default function AnnouncementsPage() {
    const [announcements, setAnnouncements] = useState<Announcement[]>([]);
    const [loading, setLoading] = useState(true);
    const [selectedCategory, setSelectedCategory] = useState("전체");
    const [searchQuery, setSearchQuery] = useState("");

    useEffect(() => {
        fetchAnnouncements();
    }, []);

    async function fetchAnnouncements() {
        setLoading(true);
        const { data, error } = await supabase
            .from("announcements")
            .select("*")
            .eq("is_active", true)
            .order("deadline", { ascending: true });

        if (!error && data) {
            setAnnouncements(data);
        }
        setLoading(false);
    }

    const filtered = announcements.filter((a) => {
        const matchCategory = selectedCategory === "전체" || a.category === selectedCategory;
        const matchSearch =
            searchQuery === "" ||
            a.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
            a.organization.toLowerCase().includes(searchQuery.toLowerCase());
        return matchCategory && matchSearch;
    });

    return (
        <div className="space-y-8">
            {/* 헤더 */}
            <div className="flex justify-between items-start">
                <div>
                    <h2 className="text-3xl font-bold tracking-tight text-slate-900 flex items-center gap-3">
                        <Megaphone className="h-8 w-8 text-blue-500" />
                        사업공고
                    </h2>
                    <p className="text-slate-500 mt-1">
                        소상공인을 위한 지원사업, 공모전, 교육 공고를 한눈에 확인하세요.
                    </p>
                </div>
                <div className="text-sm text-slate-400 bg-slate-50 px-3 py-1.5 rounded-full border border-slate-200">
                    총 {filtered.length}건
                </div>
            </div>

            {/* 검색 + 필터 */}
            <div className="flex flex-col sm:flex-row gap-4">
                <div className="relative flex-1">
                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-slate-400" />
                    <input
                        type="text"
                        placeholder="공고명 또는 기관명으로 검색..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="w-full pl-10 pr-4 py-2.5 text-sm border border-slate-200 rounded-lg bg-white text-slate-900 placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition"
                    />
                </div>
                <div className="flex gap-2 flex-wrap">
                    {CATEGORIES.map((cat) => (
                        <button
                            key={cat}
                            onClick={() => setSelectedCategory(cat)}
                            className={`px-4 py-2 rounded-lg text-sm font-medium transition-all border ${selectedCategory === cat
                                    ? "bg-blue-600 text-white border-blue-600 shadow-sm"
                                    : "bg-white text-slate-600 border-slate-200 hover:bg-slate-50 hover:border-slate-300"
                                }`}
                        >
                            {cat}
                        </button>
                    ))}
                </div>
            </div>

            {/* 공고 목록 */}
            {loading ? (
                <div className="grid gap-4 md:grid-cols-2">
                    {[...Array(4)].map((_, i) => (
                        <div key={i} className="h-48 bg-slate-100 rounded-xl animate-pulse" />
                    ))}
                </div>
            ) : filtered.length === 0 ? (
                <div className="text-center py-20 text-slate-400">
                    <Megaphone className="h-12 w-12 mx-auto mb-3 opacity-30" />
                    <p className="font-medium">해당하는 공고가 없습니다.</p>
                </div>
            ) : (
                <div className="grid gap-4 md:grid-cols-2">
                    {filtered.map((item) => {
                        const dday = getDDay(item.deadline);
                        const catColor = CATEGORY_COLORS[item.category] ?? CATEGORY_COLORS["기타"];
                        return (
                            <Link key={item.id} href={`/dashboard/announcements/${item.id}`}>
                                <Card className="group h-full hover:shadow-md hover:border-blue-200 transition-all duration-200 cursor-pointer border border-slate-200">
                                    <CardHeader className="pb-3">
                                        <div className="flex items-start justify-between gap-2 mb-2">
                                            <span
                                                className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-semibold border ${catColor.bg} ${catColor.text} ${catColor.border}`}
                                            >
                                                {item.category}
                                            </span>
                                            <span
                                                className={`text-xs font-bold px-2.5 py-0.5 rounded-full ${dday.label === "마감"
                                                        ? "bg-slate-100 text-slate-400"
                                                        : dday.urgent
                                                            ? "bg-red-50 text-red-600 border border-red-200"
                                                            : "bg-slate-50 text-slate-600 border border-slate-200"
                                                    }`}
                                            >
                                                {dday.label}
                                            </span>
                                        </div>
                                        <CardTitle className="text-base font-semibold text-slate-900 leading-snug group-hover:text-blue-600 transition-colors line-clamp-2">
                                            {item.title}
                                        </CardTitle>
                                    </CardHeader>
                                    <CardContent className="space-y-3">
                                        <CardDescription className="text-sm text-slate-500 line-clamp-2">
                                            {item.description}
                                        </CardDescription>
                                        <div className="flex items-center justify-between pt-1 border-t border-slate-100">
                                            <div className="flex items-center gap-3 text-xs text-slate-400">
                                                <span className="flex items-center gap-1">
                                                    <Building2 className="h-3.5 w-3.5" />
                                                    {item.organization}
                                                </span>
                                                <span className="flex items-center gap-1">
                                                    <Calendar className="h-3.5 w-3.5" />
                                                    {item.deadline} 마감
                                                </span>
                                            </div>
                                            <ChevronRight className="h-4 w-4 text-slate-300 group-hover:text-blue-500 transition-colors" />
                                        </div>
                                    </CardContent>
                                </Card>
                            </Link>
                        );
                    })}
                </div>
            )}
        </div>
    );
}
